import { ButtonHTMLAttributes } from "react";

import '../styles/button.scss';

type ButtonProps = ButtonHTMLAttributes <HTMLButtonElement> & { // & vai adicionar mais uma propriedade
    isOutlined?: boolean    // O sinal "?" ap√≥s o nome da propriedade indica que ela n√£o √© obrigat√≥ria 
};

export function Button({ isOutlined = false, ...props }: ButtonProps) { // "..." Rest Operator, tudo que n√£o for a primeira proprioedade (isOutlined), arquive em "props"
    return (
        <button
            className={`button ${isOutlined ? 'outlined' : ''}`}
            {...props}
        />
    )
}